/*menu */
(() => {
    const $btnMenu = document.querySelector(".menu-btn");
    const $menu = document.querySelector(".menu");

    $btnMenu.addEventListener("click", e => {
        $btnMenu.firstElementChild.classList.toggle("none");
        $btnMenu.lastElementChild.classList.toggle("none");

        $menu.classList.toggle("is-active");
    });

    document.addEventListener("click", e => {
        if (!e.target.matches(".menu a")) return false;

        $btnMenu.firstElementChild.classList.remove("none");
        $btnMenu.lastElementChild.classList.add("none");

        $menu.classList.remove("is-active");
    });
})();


/*contact form */
(() => {
    const $form = document.querySelector(".contact-form");
    const $input = document.querySelectorAll(".contact-form input")
    const $loader = document.querySelector(".contact-form-loader");
    const $response = document.querySelector(".contact-form-response");

    $input.forEach(e => {
        const $span = document.createElement("span");
        $span.id = e.name;
        $span.textContent = e.title;
        $span.classList.add("contact-form-error", "none");
        e.insertAdjacentElement("afterend", $span);
    })

    document.addEventListener("keyup", e => {
        if (e.target.matches(".contact-form input")) {
            let $input = e.target,
                pattern = e.target.pattern;

            if (pattern && $input.value) {
                let regex = new RegExp(pattern);
                return !regex.exec($input.value) ?
                    document.querySelector(`span#${$input.name}`).classList.add("is-active") :
                    document.querySelector(`span#${$input.name}`).classList.remove("is-active");
            }

            if (!$input.value) {

                document.querySelector(`span#${$input.name}`).classList.remove("is-active");

            }
        }
    })

    $form.addEventListener("submit", (e) => {
        e.preventDefault();
        $loader.classList.remove("none");
        fetch("https://formsubmit.co/ajax/a.montejano.martin@gmail.com", {
                method: "POST",
                body: new FormData(e.target),
            })
            .then((res) => (res.ok ? res.json() : Promise.reject(res)))
            .then((json) => {
                location.hash = "#gracias";
                $form.reset();
            })
            .catch((err) => {
                let message =
                    err.statusText || "Ocurrió un error al enviar, intenta nuevamente";
                $response.querySelector(
                    "h3"
                ).innerHTML = `Error ${err.status}: ${message}`;
            })
            .finally(() => {
                $loader.classList.add("none");
                setTimeout(() => {
                    location.hash = "#close";
                }, 3000);
            });
    });
})();

//counters
(() => {
    let count = document.querySelectorAll(".count")
    let arr = Array.from(count)

    arr.map(function(item) {
        let startnumber = 0

        function counterup() {
            startnumber++
            item.innerHTML = `${startnumber} <b>%</b>`;

            if (startnumber == item.dataset.number) {
                clearInterval(stop)
            }
        }

        let stop = setInterval(function() {
            counterup()
        }, 50)

    })
})();

//carousel
(() => {
    let $slides = document.querySelector(".slides"),
        $btnLeft = document.querySelector(".fa-angle-left"),
        $btnRight = document.querySelector(".fa-angle-right");

    let current = 1;

    let $dot1 = document.getElementById("slide-1"),
        $dot2 = document.getElementById("slide-2"),
        $dot3 = document.getElementById("slide-3")

    setInterval(() => {
        current++;
        if (current > 3) {
            current = 1;
        }
        moveSlide(current)
    }, 5000);

    //Evento para botón derecho
    $btnRight.addEventListener("click", function() {
        current++;
        if (current > 3) {
            current = 1;
        }

        moveSlide(current);
    });

    //Evento para botón izquierdo
    $btnLeft.addEventListener("click", function() {
        current--;
        if (current < 1) {
            current = 3;
        }

        moveSlide(current);
    });

    $dot1.addEventListener("click", e => {
        moveSlide(1);
    })

    $dot2.addEventListener("click", e => {
        moveSlide(2);
    })

    $dot3.addEventListener("click", e => {
        moveSlide(3);
    })

    function moveSlide(current) {
        if (current === 1) {
            $slides.style.transform = "translateX(0%)";
            $dot1.checked = true;
        }

        if (current === 2) {
            $slides.style.transform = "translateX(-33.3333333333%)";
            $dot2.checked = true;
        }

        if (current === 3) {
            $slides.style.transform = "translateX(-66.6666666666%)";
            $dot3.checked = true;
        }
    }
})();

//observer
const scrollSpy = () => {

    const $sections = document.querySelectorAll(".dpart")

    const cb = (entries) => {
        entries.forEach(e => {
            const id = e.target.getAttribute("id");

            if (e.isIntersecting) {
                document.querySelector(`a[data-scroll-spy][href="#${id}"]`).classList.add("active-spy");
            } else {
                document.querySelector(`a[data-scroll-spy][href="#${id}"]`).classList.remove("active-spy");
            }
        })
    }

    const observer = new IntersectionObserver(cb, {
        rootMargin: "-250px"
    })

    $sections.forEach(e => observer.observe(e));
}

//bottón ir arriba
document.addEventListener("scroll", e => {
    if (window.scrollY > 200) {
        document.getElementById("buttonGoTop").style.visibility = "visible";
        document.getElementById("buttonGoTop").style.opacity = "1";
    } else {
        document.getElementById("buttonGoTop").style.visibility = "hidden";
        document.getElementById("buttonGoTop").style.opacity = "0";
    }

    scrollSpy();
})